import { checkNum } from 'helper/calculateMatrix';

export const getLightGate = (info, lng) => {
  const {
    lightLeft1,
    lightLeft2,
    lightRight2,
    lightRight1,
    lightTop1,
    lightTop2,
    lightBottom2,
    lightBottom1,
    lightTopLeft1,
    outCircle,
    innerCircle,
    lightTopLeft2,
    lightBottomLeft1,
    lightBottomLeft2,
    lightTopRight1,
    lightTopRight2,
    lightBottomRight1,
    lightBottomRight2,
    day,
    left2,
    left3,
    right2,
    year,
    right3,
    month,
    top2,
    top3,
    bottom3,
    bottom2,
    bottom1,
    topLeft1,
    topLeft2,
    topLeft3,
    bottomRight1,
    bottomRight2,
    bottomRight3,
    topRight1,
    topRight2,
    topRight3,
    bottomLeft1,
    bottomLeft2,
    bottomLeft3,
  } = info;

  const data = [
    {
      karmaName: lng === 'ua' ? ' ' : ' ',
      info: [
        {
          type: '',
          energyInfo: [
            {
              name: lng === 'ua' ? ' ' : ' ',
              energy: `${day} - ${left2} - ${lightLeft1}`,
            },
            {
              name: lng === 'ua' ? '   ' : '   ',
              energy: `${left2} - ${left3} - ${lightLeft2}`,
            },
            {
              name: lng === 'ua' ? " '" : ' ',
              energy: `${year} - ${right2} - ${lightRight2}`,
            },
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: `${right3} - ${right2} - ${lightRight1}`,
            },
          ],
        },
        {
          type: '',
          energyInfo: [
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: `${month} - ${top2} - ${lightTop1}`,
            },
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: `${top3} - ${top2} - ${lightTop2}`,
            },
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: `${bottom1} - ${bottom2} - ${lightBottom1}`,
            },
            {
              name:
                lng === 'ua'
                  ? '   '
                  : '   ',
              energy: `${bottom3} - ${bottom2} - ${lightBottom2}`,
            },
          ],
        },
      ],
    },
    {
      karmaName: lng === 'ua' ? ' ' : ' ',
      info: [
        {
          type: lng === 'ua' ? '' : '',
          energyInfo: [
            {
              name:
                lng === 'ua'
                  ? '    '
                  : '    ',
              energy: `${topLeft1} - ${topLeft2} - ${lightTopLeft1}`,
            },
            {
              name:
                lng === 'ua' ? '   ' : '   ',
              energy: `${topLeft3} - ${topLeft2} - ${lightTopLeft2}`,
            },
            {
              name:
                lng === 'ua'
                  ? '  , '
                  : '  , ',
              energy: `${bottomRight1} - ${bottomRight2} - ${lightBottomRight1}`,
            },
            {
              name:
                lng === 'ua'
                  ? '    '
                  : '    ',
              energy: `${bottomRight3} - ${bottomRight2} - ${lightBottomRight2}`,
            },
          ],
        },
        {
          type: lng === 'ua' ? '' : '',
          energyInfo: [
            {
              name:
                lng === 'ua'
                  ? '   '
                  : '    ',
              energy: `${topRight1} - ${topRight2} - ${lightTopRight1}`,
            },
            {
              name:
                lng === 'ua' ? '   ' : '   ',
              energy: `${topRight3} - ${topRight2} - ${lightTopRight2}`,
            },
            {
              name:
                lng === 'ua'
                  ? '   '
                  : '   ',
              energy: `${bottomLeft1} - ${bottomLeft2} - ${lightBottomLeft1}`,
            },
            {
              name:
                lng === 'ua'
                  ? '    '
                  : '    ',
              energy: `${bottomLeft3} - ${bottomLeft2} - ${lightBottomLeft2}`,
            },
          ],
        },
      ],
      total: {
        name: lng === 'ua' ? ' ' : ' ',
        keys: [outCircle, innerCircle],
      },
    },
  ];

  const healing = {
    tablesName: lng === 'ua' ? '   ' : '   ',
    tables: [
      {
        rowName: [
          lng === 'ua' ? '0 - 5 ' : '0 - 5 ',
          '',
          lng === 'ua' ? '35 - 45 ' : '35 - 45 ',
          '',
        ],
        keys: getLightGateHealing(
          [day, left2, left3, lightLeft1, lightLeft2],
          [year, right2, right3, lightRight1, lightRight2]
        ),
      },
      {
        rowName: [
          lng === 'ua' ? '5 - 15 ' : '5 - 15 ',
          '',
          lng === 'ua' ? '45 - 55 ' : '45 - 55 ',
          '',
        ],
        keys: getLightGateHealing(
          [topLeft1, topLeft2, topLeft3, lightTopLeft1, lightTopLeft2],
          [bottomRight1, bottomRight2, bottomRight3, lightBottomLeft1, lightBottomLeft2]
        ),
      },
      {
        rowName: [
          lng === 'ua' ? '15 - 25 ' : '15 - 25 ',
          '',
          lng === 'ua' ? '55 - 65 ' : '55 - 65 ',
          '',
        ],
        keys: getLightGateHealing(
          [month, top2, top3, lightTop1, lightTop2],
          [bottom1, bottom2, bottom3, lightBottom1, lightBottom2]
        ),
      },
      {
        rowName: [
          lng === 'ua' ? '25 - 35 ' : '25 - 35 ',
          '',
          lng === 'ua' ? '65 - 75 ' : '65 - 75 ',
          '',
        ],
        keys: getLightGateHealing(
          [topRight1, topRight2, topRight3, lightTopRight1, lightTopRight2],
          [bottomLeft1, bottomLeft2, bottomLeft3, lightBottomLeft1, lightBottomLeft2]
        ),
      },
    ],
  };

  return { karma: data, healing };
};

const getLightGateHealing = (topKeys, bottomKeys) => {
  const keys = topKeys.map((el, index) => ({
    youngKey: `${el} - ${bottomKeys[index]} - ${checkNum(el + bottomKeys[index])}`,
    oldKey: `${bottomKeys[index]} - ${el} - ${checkNum(el + bottomKeys[index])}`,
  }));
  return keys;
};
